import {
    Component,
    h,
    Prop,
    State,
    Element,
    Event,
    EventEmitter,
    Host,
} from '@stencil/core';
import { fetchTranslations } from '../../utils/translation';
import { cva } from 'class-variance-authority';
import { SelectOption } from '../../types/select';

const inputVariants = cva(
    'min-h-36 placeholder-text-light relative flex min-h-[36px] w-full flex-shrink flex-grow basis-0 gap-4 rounded-md border border-solid py-4 pl-8 pr-24 outline-none transition-colors duration-300 ease-in-out focus-within:outline-0 focus-within:ring-2',
    {
        variants: {
            disabled: {
                false: 'bg-white focus-within:ring-active-foreground/30',
                true: 'pointer-events-none border-none bg-surface-1 !text-disabled',
            },
            readonly: {
                false: 'focus-within:ring-active-foreground/30',
                true: 'cursor-text border-none !bg-surface-1 !text-dark',
            },
            invalid: {
                false: 'border-med focus-within:border-active-dark focus-within:ring-active-foreground/30',
                true: 'border-error-base focus-within:border-error-base focus-within:ring-destructive-foreground/30',
            },
            typeahead: {
                false: 'cursor-pointer caret-transparent',
                true: null,
            },
        },
        defaultVariants: {
            disabled: false,
            readonly: false,
            invalid: false,
            typeahead: false,
        },
    },
);

const searchVariants = cva(
    'transition[background-color,color] h-24 flex-shrink flex-grow basis-0 rounded-md p-8 outline-0 duration-300 ease-in-out',
    {
        variants: {
            typeahead: {
                true: 'focus:bg-surface-1',
                false: 'cursor-pointer bg-transparent caret-transparent',
            },
            disabled: {
                true: 'pointer-events-none bg-transparent',
                false: 'bg-surface-base',
            },
        },
        defaultVariants: {
            disabled: false,
            typeahead: false,
        },
    },
);

const optionVariants = cva(
    'transition[background-color,color,box-shadow] flex w-full cursor-pointer items-center truncate p-8 text-body font-normal duration-300 ease-in-out focus:outline-0 focus:ring-2 focus:ring-inset focus:ring-active-foreground/40',
    {
        variants: {
            active: {
                true: 'bg-active-light text-active',
                false: 'bg-white hover:bg-disabled-light',
            },
        },
    },
);

/**
 * @category Form Controls
 * @description A multi-selection dropdown component for choosing multiple values from a list of options. Features search functionality, keyboard navigation, and accessibility support.
 */
@Component({
    tag: 'atui-multi-select',
    shadow: false,
})
export class AtuiMultiSelectComponent {
    /**
     * Options displayed in the dropdown menu.
     */
    @Prop()
    options: SelectOption[];

    /**
     * Label of the select.
     */
    @Prop()
    label?: string = '';

    /**
     * Short description or validation hint if required.
     */
    @Prop()
    hint_text?: string = '';

    /**
     * Error text for the select.
     */
    @Prop()
    error_text?: string = '';

    /**
     * Optional info icon with detailed tooltip description. Displayed at right of label.
     */
    @Prop()
    info_text?: string = '';

    /**
     * Placeholder text for the select.
     */
    @Prop()
    placeholder?: string = '';

    /**
     * Indicated form field is required.
     */
    @Prop()
    required?: boolean;

    /**
     * Set the select to appear invalid.
     */
    @Prop()
    invalid?: boolean;

    /**
     * Set the select to be clearable.
     */
    @Prop()
    clearable?: boolean;

    /**
     * Disable user interaction. Disabled state should be applied via form control.
     */
    @Prop()
    disabled?: boolean;

    /**
     * Set input to readonly mode, allows users to select any active values.
     */
    @Prop()
    readonly?: boolean;

    /**
     * Display selected options outside of input. In readonly mode selected options are displayed as chips within input.
     */
    @Prop()
    chip_list?: boolean;

    /**
     * Set the select to enable typeahead search.
     */
    @Prop()
    typeahead?: boolean;

    /**
     * The selected items
     */
    @Prop({ mutable: true })
    value: string[] = [];

    @State() searchText: string = '';
    @State() isOpen: boolean = false;
    @State() translations: any;
    @State() hasMatchingOptions: boolean = false;
    @State() parentWidth: string = '';

    @Element() el: HTMLElement;
    private inputEl!: HTMLInputElement;
    private menuId = `dropdown-${Math.random().toString(36).substring(2, 11)}`;
    private menuRef: HTMLAtuiMenuElement;
    private optionEls: HTMLLIElement[] = [];

    /**
     * Emits an event containing a list of the selected items when the selection changes.
     */
    @Event()
    atuiChange: EventEmitter<string[]>;

    async componentWillLoad() {
        this.translations = await fetchTranslations(this.el);
    }

    componentDidLoad() {
        this.el
            .querySelectorAll('li[data-name="select-option"]')
            .forEach((option) => {
                this.optionEls.push(option as HTMLLIElement);
            });
        const parentRect = this.el.getBoundingClientRect();
        this.parentWidth = `${parentRect.width}px`;
    }

    private updateIsOpenState(event: CustomEvent<boolean>): void {
        this.isOpen = event.detail;
    }

    private handleChange(option: string) {
        this.value = this.value.includes(option)
            ? this.value.filter((item) => item !== option)
            : [...this.value, option];
        this.inputEl.value = '';
        this.searchText = '';
        this.atuiChange.emit(this.value);
    }

    handleClear() {
        this.searchText = '';
        this.value = [];
        this.inputEl.value = '';
        this.inputEl.focus();
        this.atuiChange.emit(this.value);
    }

    private focusOption(relativePosition: number): void {
        const indexOfActiveOption = this.optionEls.indexOf(
            this.el.ownerDocument.activeElement as HTMLLIElement,
        );
        const nextOptionEl =
            this.optionEls[
                (indexOfActiveOption +
                    relativePosition +
                    this.optionEls.length) %
                    this.optionEls.length
            ];
        nextOptionEl.focus();
    }

    private handleSearchChange(event: KeyboardEvent): void {
        this.searchText = (
            event.target as HTMLInputElement
        ).value.toLowerCase();

        const trimmedSearch = this.searchText.trim().toLowerCase();
        this.hasMatchingOptions = trimmedSearch
            ? this.options?.some((option) =>
                  option.value.toLowerCase().includes(trimmedSearch),
              )
            : false;
    }

    private async handleKeyDownInput(event: KeyboardEvent): Promise<void> {
        switch (event.key) {
            case 'Enter':
            case 'Space':
                await this.menuRef.toggleMenu();
                break;
            case 'Escape':
                await this.menuRef.closeMenu();
                break;
            default:
                this.handleSearchChange(event);
                await this.menuRef.openMenu();
                break;
        }
    }

    private async handleKeyDownMenu(event: KeyboardEvent): Promise<void> {
        switch (event.key) {
            case 'Escape':
                await this.menuRef.closeMenu;
                break;
            case 'Enter':
            case ' ':
                if (event.target instanceof HTMLLIElement) {
                    event.target.click();
                }
                break;
            case 'ArrowUp':
            case 'ArrowLeft':
                event.preventDefault();
                this.focusOption(-1);
                break;
            case 'ArrowDown':
            case 'ArrowRight':
                event.preventDefault();
                this.focusOption(1);
                break;
        }
    }

    render() {
        return (
            <Host
                class="relative"
                onFocusout={async (event: FocusEvent): Promise<void> => {
                    if (!this.el.contains(event.relatedTarget as Node)) {
                        await this.menuRef?.closeMenu();
                    }
                }}
            >
                {this.renderLabel()}
                <atui-menu
                    ref={(el) => (this.menuRef = el)}
                    trigger="click"
                    align="start"
                    role="listbox"
                    width={this.parentWidth}
                    autoclose={false}
                    portal={true}
                    disabled={this.disabled || this.readonly}
                    onAtuiMenuStateChange={(event) =>
                        this.updateIsOpenState(event)
                    }
                >
                    {this.renderInput()}
                    {!this.disabled || !this.readonly
                        ? this.renderOptions()
                        : null}
                </atui-menu>

                <div>
                    {this.error_text && this.invalid && (
                        <span
                            data-name="multi-select-error"
                            class="text-error"
                        >
                            {this.error_text}
                        </span>
                    )}
                    {this.chip_list && (
                        <atui-chip-list
                            onAtuiRemoveChip={(event) =>
                                (this.value = this.value.filter(
                                    (item) => !event.detail.includes(item),
                                ))
                            }
                            chips={this.value}
                            data-name="multi-select-chips-outside"
                        />
                    )}
                </div>
            </Host>
        );
    }

    renderLabel() {
        return (
            <div class="mb-4 flex flex-col">
                <slot name="label" />
                {(this.label || this.required || this.info_text) && (
                    <atui-form-label
                        for={this.menuId}
                        label={this.label}
                        required={this.required && !this.readonly}
                        info_text={this.info_text}
                    />
                )}
                {this.hint_text && (
                    <span
                        class="text-xs leading-tight text-med"
                        data-name="multi-select-hint"
                    >
                        {this.hint_text}
                    </span>
                )}
            </div>
        );
    }

    renderInput() {
        const classname = inputVariants({
            invalid: this.invalid,
            disabled: this.disabled,
            readonly: this.readonly,
            typeahead: this.typeahead,
        });

        const searchClassname = searchVariants({
            typeahead: this.typeahead,
        });

        return (
            <div
                class={classname}
                tabIndex={-1}
                slot="menu-trigger"
            >
                <atui-chip-list
                    size="sm"
                    class="w-full"
                    readonly={this.readonly}
                    disabled={this.disabled}
                    show_clear_all={false}
                    onAtuiRemoveChip={(event) => {
                        if (
                            !this.disabled &&
                            !this.readonly &&
                            this.typeahead
                        ) {
                            this.inputEl.focus();
                        }
                        this.value = this.value.filter(
                            (item) => !event.detail.includes(item),
                        );
                    }}
                    chips={this.chip_list ? [] : this.value}
                    tabIndex={-1}
                    data-name="multi-select-chips-inside"
                >
                    {this.typeahead && (
                        <input
                            id={`input-${this.menuId}`}
                            role="combobox"
                            aria-autocomplete={
                                this.typeahead ? 'list' : undefined
                            }
                            aria-haspopup={!this.chip_list ? true : undefined}
                            aria-controls={this.menuId}
                            class={searchClassname}
                            type="text"
                            readOnly={this.readonly}
                            placeholder={this.placeholder}
                            onKeyDown={async (event) => {
                                await this.handleKeyDownInput(event);
                            }}
                            data-name="multi-select-search-input"
                            ref={(el) => (this.inputEl = el)}
                        />
                    )}
                </atui-chip-list>

                {!this.readonly && !this.disabled && (
                    <div class="absolute right-4 flex items-center">
                        {this.clearable && (
                            <atui-button
                                class={`transition-all duration-300 ease-in-out ${
                                    !!this.value.length || this.searchText
                                        ? 'scale-100 opacity-100'
                                        : 'pointer-events-none scale-90 opacity-0'
                                }`}
                                size="sm"
                                icon="cancel"
                                type="secondaryText"
                                onClick={(event) => {
                                    event.stopPropagation();
                                    this.handleClear();
                                }}
                                data-name="multi-select-clear"
                            ></atui-button>
                        )}
                        <atui-button
                            size="sm"
                            class="!i>text-light h-[18pz] w-[18px]"
                            icon={
                                this.isOpen
                                    ? 'arrow_drop_up'
                                    : 'arrow_drop_down'
                            }
                            type="secondaryText"
                            onClick={async () => {
                                await this.menuRef.toggleMenu;
                            }}
                        ></atui-button>
                    </div>
                )}
            </div>
        );
    }

    renderOptions() {
        return (
            <ul
                id={this.menuId}
                role="listbox"
                class="contents"
                slot="menu-content"
                onKeyDown={async (event) => {
                    await this.handleKeyDownMenu(event);
                }}
            >
                {this.options
                    ?.filter((option) =>
                        option.value.toLowerCase().includes(this.searchText),
                    )
                    .map((option) => this.renderOption(option))}

                {this.typeahead && !this.hasMatchingOptions && (
                    <div class="w-full bg-white px-16 py-8 text-body text-light">
                        {this.translations.ATUI.NO_RESULTS_FOUND}
                    </div>
                )}
            </ul>
        );
    }

    renderOption(option: SelectOption) {
        const classname = optionVariants({
            active: this.value.includes(option.value),
        });

        return (
            <li
                role="option"
                data-name="multi-select-option"
                aria-selected={this.value.includes(option.value)}
                tabIndex={0}
                class={classname}
                onClick={() => this.handleChange(option.value)}
            >
                <i
                    role="presentation"
                    class={`material-icons h-16 w-16 text-sm ease-in-out ${
                        this.value.includes(option.value) ? 'visible' : 'hidden'
                    }`}
                >
                    check
                </i>
                {option.value}
            </li>
        );
    }
}
